body {
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    margin: 0;
}


.app {
    --background-color: #F0D9A9;
    --font-color: #333;
    --accent-color: orangered;
    --transition-time: 400ms;

    background-color: var(--background-color);
    transition: background-color var(--transition-time);
    width: 100vw;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.app.dark {
    --background-color: #333;
    --font-color: #F0D9A9;
}

.board {
    background-color: var(--font-color);
    transition: var(--transition-time);
    width: 460px;
    height: 460px;
    display: flex;
    justify-content: space-between;
    align-content: space-between;
    flex-wrap: wrap;
}

.board-field {
    background-color: var(--background-color);
    transition: var(--transition-time);
    color: var(--font-color);
    width: 150px;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 32px;
    font-weight: bold;
    cursor: pointer;
    position: relative;
    display: flex;
}

.board-field:hover {
    background-color: var(--accent-color);
}

.panel {
    width: 230px;
    padding: 30px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    height: 460px;
    justify-content: space-between;
}

.score {
    color: var(--font-color);
    transition: var(--transition-time);
    font-weight: bold;
    font-size: 22px;
}

.controls {
    display: flex;
    flex-direction: column;
}

.control-button {
    background-color: var(--font-color);
    color: var(--background-color);
    transition: var(--transition-time);
    border: none;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 10px;
    font-weight: bold;
    font-size: 14px;
    cursor: pointer;
    scale: 1;
}

.control-button:hover {
    background-color: var(--accent-color);
    transform: scale(1.1);
}

@media (max-width: 460px) {
    .app {
        align-content: center;
    }

    .board {
        width: 310px;
        height: 310px;
    }

    .board-field {
        width: 100px;
        height: 100px;
    }

    .score {
        font-size: 18px;
    }
}

@media (max-width: 690px) {
    .panel {
        height: auto;
        flex-direction: row;
        width: 100%;
    }

    .scoreboard {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}

.OFFSET {
    --background-color: #F0D9A9;
    --font-color: #333;
    --accent-color: orangered;
    --transition-time: 400ms;

    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
}

.OFFSET.dark {
    --background-color: #333;
    --font-color: #F0D9A9;
}

.about {
    border-radius: 10px;
    z-index: 11;
    width: 300px;
    background-color: var(--background-color);
    color: var(--font-color);
    position: relative;
    padding: 15px 25px;
    box-sizing: border-box;
}

.about-h {
    margin: 0;
}

.about-text {
    font-size: 18px;
    line-height: 1.5rem;
}

.about-ul {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.about-i {
    color: var(--font-color);
    font-size: 38px;
    transition: var(--transition-time);
}

.about-i:hover {
    color: var(--accent-color);
    transform: scale(1.1);
}

.about-button {
    position: absolute;
    top: 10px;
    right: 10px;
    border: none;
    background-color: var(--font-color);
    color: var(--background-color);
    font-size: 20px;
    width: 36px;
    height: 36px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: var(--transition-time);
}

.about-button:hover {
    background-color: var(--accent-color);
    transform: scale(1.1);
    cursor: pointer;
}

.matchResult {
    border-radius: 10px;
    z-index: 11;
    background-color: var(--background-color);
    color: var(--font-color);
    position: relative;
    padding: 15px 25px;
    box-sizing: border-box;
    font-weight: bold;
    min-width: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.welcome {
    border-radius: 10px;
    z-index: 11;
    background-color: var(--background-color);
    color: var(--font-color);
    position: relative;
    padding: 15px 25px;
    box-sizing: border-box;
    font-weight: bold;
    max-width: 320px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.welcome-h {
    font-size: 20px;
}

.welcome-title {
    font-size: 18px;
}

.welcome-buttons {
    display: flex;
    flex-direction: row;
}

.welcome-button {
    background-color: var(--font-color);
    color: var(--background-color);
    transition: var(--transition-time);
    border: none;
    width: 36px;
    height: 36px;
    margin: 0 10px;
    border-radius: 10px;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    scale: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.welcome-button:hover {
    background-color: var(--accent-color);
    transform: scale(1.1);
}